* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;

  background-repeat: no-repeat;
  background-size: cover;
 
  background-image: url(/src/bg2.jpg);
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info__about{
  display: flex;
  align-items: center;
}

.country-info__name {
  margin: 0 20;
  font-size:36px;
  font-weight: 700;
  
}

.country-info__text {
  font-weight: 700;
  font-size: 20px;
}

.country-list__item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-list__name{
  margin: 0 20;
  font-size: 16px;
  font-weight: 700;
 }